import request from '../utils/request.js'
// Login api
// 
export const login = data => {
  return request.post('/login', data)
}

// Home api
// 
export const getMenusList = () => {
  return request.get('/menus')
}

// Users api
// 
export const getUsersList = data => {
  return request.get('/users', { params: data })
}
// 
export const userStateChanged = data => {
  return request.put(`/users/${data.uId}/state/${data.type}`)
}
// 
export const addUser = data => {
  return request.post('/users', data)
}
// ID
export const getUserById = id => {
  return request.get(`users/${id}`)
}
// 
export const editUser = (id, data) => {
  return request.put(`users/${id}`, data)
}
// ID
export const removeUserById = id => {
  return request.delete(`users/${id}`)
}
//  Roles api

// 
export const allotRole = (id, rid) => {
  return request.put(`users/${id}/role`, { rid: rid })
}

// Rights api
// 
export const getRightsList = type => {
  return request.get(`rights/${type}`)
}

// Roles api
// 
export const getRolesList = () => {
  return request.get('/roles')
}
// 
export const addRole = data => {
  return request.post('/roles', data)
}
// ID
export const getRoleById = id => {
  return request.get(`roles/${id}`)
}
// 
export const editRole = (id, data) => {
  return request.put(`Roles/${id}`, data)
}
// ID
export const removeRoleById = id => {
  return request.delete(`Roles/${id}`)
}
// ID
export const removeRightById = (roleId, rightId) => {
  return request.delete(`roles/${roleId}/rights/${rightId}`)
}
//  roleIdid  ridsid
export const allotRights = (roleId, rids) => {
  return request.post(`roles/${roleId}/rights`, { rids: rids })
}

// Cate api
// 
export const getCateList = data => {
  return request.get('/categories', { params: data })
}
// 
export const addCate = data => {
  return request.post('/categories', data)
}
// 
export const getParamsList = (id, sel) => {
  return request.get(`/categories/${id}/attributes`, { params: { sel: sel } })
}
// &
export const addParams = (id, data) => {
  return request.post(`categories/${id}/attributes`, data)
}
// &
export const getParams = (id, attrId, attr_sel) => {
  return request.get(`categories/${id}/attributes/${attrId}`, { params: { attr_sel: attr_sel } })
}
// &
export const editParams = (id, attrId, attr_name, attr_sel, attr_vals) => {
  return request.put(`categories/${id}/attributes/${attrId}`, {
    attr_name: attr_name,
    attr_sel: attr_sel,
    attr_vals: attr_vals
  })
}
// &
export const removeParams = (id, attrId) => {
  return request.delete(`categories/${id}/attributes/${attrId}`)
}

// List api
// 
export const getGoodsList = data => {
  return request.get('/goods', { params: data })
}
// 
export const removeById = id => {
  return request.delete(`/goods/${id}`)
}
// 
export const addGood = data => {
  return request.post('/goods', data)
}

// order api
// 
export const getOrderList = data => {
  return request.get('/orders', { params: data })
}
// 
export const getkuaidi = (id = 1106975712662) => {
  return request.get(`/kuaidi/${id}`)
}

// report api
// 
export const getreport = () => {
  return request.get('reports/type/1')
}
